%button{
    background: #fff;
    font-size: 16px;
    font-weight: 500;
    border: 1px solid transparent;
    color: #ff3500;
    padding: 17px 50px;
    font-family: 'Montserrat', sans-serif;
    display: inline-block;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
}
.boxed-btn4{
    @extend %button;
    border-radius: 30px;


}